"use client";

// React and Next
import { useEffect, useState } from "react";
import { useTranslation } from "react-i18next";

// API
import { TrackOrderApi } from "@/api/clientsideaction/order";

// Utils
import { imageUrl } from "@/utilities/url";

// 3rd Party
import moment from "moment";

// Components
import Sidebar from "@/Components/Myprofile/sidebar";
import Breadcrumb from "@/Components/Breadcrumbs";
import CustomCurrencyFormatter from "@/Components/CustomCurrencyFormatter";

export default function TrackOrder({ productId }) {
  const { t } = useTranslation();
  const [statusCheck, setStatusCheck] = useState(0);
  const [trackdata, setTrackData] = useState();
    const [langCode, setLangCode] = useState(null);
  const [orderData, setOrderData] = useState(0);

  useEffect(() => {
    const code = localStorage.getItem("languageCode");
    setLangCode(code);
  }, []);

  useEffect(() => {
    TrackOrderApi(productId, setTrackData, setStatusCheck);
  }, []);
  const orderBreadCrumbEdit = [
    {
      text: t("Breadcrumb.home"),
      url: "/",
    },
    {
      text: t("Breadcrumb.accountSettings"),
      url: "/account-settings",
    },
    {
      text: t("Breadcrumb.orderHistory"),
      url: "/account-settings/order-history",
    },
    {
      text: t("Breadcrumb.trackOrder"),
      url: "javascript:void(0)",
    },
  ];
  const convertLink = (link) => {
    if (!/^https?:\/\//i.test(link)) {
      link = "http://" + link;
      return link;
    } else {
      return link;
    }
  };

  return (
    <>
      <div className="mx-auto px-4 py-2.5 max-w-screen-2xl">
        <Breadcrumb breadCrumb={orderBreadCrumbEdit} />

        <div className="flex mx-auto mt-4 mb-7">
          <Sidebar />

          <div className="w-full px-0 py-6 pr-0 overflow-auto sm:px-8 sm:pr-4">
            <h3 className="mb-6 text-lg font-black text-black-300">
              {t("orderhistory.TrkOrd")}
            </h3>
            <div className="p-6 border border-[#D1DADF] rounded-[5px] w-full">
              <div className="flex flex-wrap justify-between gap-6 mb-4">
                <div className="flex sm:flex-row flex-col items-center sm:items-start sm:rtl:space-x-[unset] sm:space-x-4 space-y-4 sm:space-y-0">
                  <div className="w-20 sm:rtl:ml-4 min-w-20">
                    <img
                      src={
                        imageUrl +
                        "?path=" +
                        trackdata?.containerName +
                        "&name=" +
                        trackdata?.productImage +
                        "&width=400&height=200"
                      }
                      alt={trackdata?.productName}
                      onError={({ currentTarget }) => {
                        currentTarget.onerror = null;
                        currentTarget.src = "/img/no-image.png";
                        currentTarget.alt = "no-image";
                      }}
                      className="object-contain w-full max-w-full"
                    />
                  </div>

                  <div className="flex flex-col justify-between items-start space-y-4 max-w-[576px]">
                    <h3 className="text-base font-medium text-black-300">
                      {trackdata?.productName}
                    </h3>
                    <p className="text-lg font-black text-black-300">
                      <CustomCurrencyFormatter price={trackdata?.basePrice} />
                    </p>
                  </div>
                </div>
                <div className="flex flex-col uppercase">
                  <h3 className="font-bold text-[#B3B3C4] text-xs">
                    {t("orderhistory.ShipAddr")}
                  </h3>
                  <p className="text-xs font-bold text-black-300">
                    {trackdata?.shippingAddress1}
                  </p>
                  <p className="text-xs font-bold text-black-300">
                    {trackdata?.shippingAddress2}
                  </p>
                  <p className="text-xs font-bold text-black-300">
                    {trackdata?.shippingCity}
                  </p>
                  <p className="text-xs font-bold text-black-300">
                    {trackdata?.shippingPostcode}
                  </p>
                </div>
                {trackdata?.trackingId && <div className="flex flex-col uppercase">
                  <h3 className="font-bold text-[#B3B3C4] text-xs">
                    {t("orderhistory.TrkID")} :{" "}
                  </h3>
                  {/* <p className="text-xs font-bold text-black-300">
                    # SPU-20240607101431
                  </p> */}
                  {trackdata?.trackingId && trackdata?.trackingUrl == null ? (
                    `${trackdata?.trackingId}`
                  ) : (
                    <a
                      className="text-xs font-bold text-black-300"
                      href={convertLink(trackdata?.trackingUrl)}
                      target="_blank"
                    >
                      {trackdata?.trackingId}
                    </a>
                  )}
                </div>}
              </div>
              <div className="flex flex-col space-y-[5px] mr-auto xl:px-24 pl-0 2xl:pl-[7rem] overflow-hidden">
                <a
                  role="button"
                  className="text-xs underline cursor-pointer text-link text-end"
                  onClick={() => setOrderData(true)}
                >
                  {t("allcategory.ViewMore")}
                </a>
                <div className="flex py-[5px] overflow-hidden">
                  {trackdata &&
                    trackdata?.deliveryStatus?.map(
                      (result, index) =>
                        index <= 5 && (
                          <div
                            key={index}
                            className={`${
                              index <= statusCheck
                                ? "relative active"
                                : "relative"
                            }`}
                          >
                            <div className="bg-line-active rounded-[10px] w-[120px] min-w-[120px] h-2"></div>
                            <div className="top-[-5px] right-0 absolute flex flex-col gap-[6px]">
                              <div className="flex justify-center items-center bg-active ml-auto border border-white rounded-full w-[18px] h-[18px]">
                                <div className="flex justify-center items-center bg-inner rounded-full w-2.5 h-2.5">
                                  <img
                                    src="/img/track-tick.svg"
                                    alt="track-tick"
                                  />
                                </div>
                              </div>
                            </div>
                            <div className="flex flex-col items-start gap-0.5 mt-3 ml-[85%] w-full">
                              <h3 className="text-xs font-bold text-black-300">
                                {result?.name}
                              </h3>
                              <p className="max-w-[52px] font-medium text-[#989898] text-xs italic">
                                {result?.createdDate &&
                                  moment(result?.createdDate)?.format(
                                    "DD MMM, YYYY, hh:mm A"
                                  )}
                              </p>
                            </div>
                          </div>
                        )
                    )}

                  <div className="">
                    <div className="bg-line-active rounded-[10px] w-[120px] min-w-[120px] h-2"></div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>

      <div
        aria-modal="true"
        id="static-modal"
        data-modal-backdrop="static"
        tabIndex="-1"
        aria-hidden="true"
        className={`${
          orderData
            ? "overflow-y-auto overflow-x-hidden fixed top-0 right-0 left-0 z-100 justify-center bg-light-modal items-center w-full md:inset-0 h-full max-h-full flex sm:px-0 px-4 py-8 sm:py-16"
            : " hidden"
        }`}
      >
        <div className="relative w-full max-w-[464px] h-full max-h-[672px] overflow-auto">
          {/* <!-- Modal content --> */}
          <div className="relative bg-white shadow dark:bg-gray-700">
            {/* <!-- Modal header --> */}
            <div className="top-0 z-[5] sticky bg-white">
              <div className="top-4 sm:top-6 right-4 sm:right-6 z-[10] absolute">
                <button
                  type="button"
                  data-modal-hide="static-modal"
                  onClick={() => setOrderData(false)}
                >
                  <img src="/img/modal-x.svg" alt="modal-x" />
                </button>
              </div>
              <div className="p-8 pb-6">
                <h3 className="mb-3 text-base font-bold text-black-300">
                  {t("orderhistory.TrkOrd")}{" "}
                  {trackdata?.trackingId && trackdata?.trackingUrl == null ? (
                    `${trackdata?.trackingId}`
                  ) : (
                    <a
                      href={convertLink(trackdata?.trackingUrl)}
                      hrefLang={langCode}
                      target="_blank"
                    >
                      {trackdata?.trackingId}
                    </a>
                  )}
                </h3>
                <div className="flex justify-between items-start flex-col sm:flex-row sm:space-x-6 sm:space-y-0 space-y-3 rtl:space-x-[unset]">
                  <p className="flex sm:rtl:space-x-[unset] sm:space-x-1 space-y-1 rtl:ml-6 font-normal text-black-300 text-sm">
                    <span>{t("orderhistory.OrdID")}</span> :
                    <span className="m-0"> {trackdata?.orderPrefixId}</span>
                  </p>
                  <span className="text-sm font-normal text-black-300">
                    {moment(trackdata?.orderStatusDate)?.format(
                      "DD MMM, YYYY, hh:mm A"
                    )}
                  </span>
                </div>
              </div>
            </div>
            <div className="p-8 pt-0">
              <div className="flex flex-col px-[5px]">
                {trackdata &&
                  trackdata?.deliveryStatus?.map((result, index) => (
                    <div
                      key={index}
                      className={`${
                        index <= statusCheck
                          ? "relative active flex gap-[21px]"
                          : "relative flex gap-[21px]"
                      }`}
                    >
                      <div className="bg-line-active rounded-[10px] w-full max-w-2 h-16 min-h-16"></div>
                      <div className="bottom-0 left-[-5px] absolute flex flex-col gap-[6px]">
                        <div className="flex justify-center items-center bg-active border border-white rounded-full w-[18px] h-[18px]">
                          <div className="flex justify-center items-center bg-inner rounded-full w-2.5 h-2.5">
                            <img src="/img/track-tick.svg" alt="track-tick" />
                          </div>
                        </div>
                      </div>
                      <div className="top-[46px] relative flex flex-col items-start gap-0.5 w-auto">
                        <h3 className="text-sm font-bold text-black-300">
                          {result?.name}
                        </h3>
                        <p className="max-w-[73px] font-medium text-[#989898] text-xs italic">
                          {result?.createdDate &&
                            moment(result?.createdDate)?.format(
                              "DD MMM, YYYY, hh:mm A"
                            )}
                        </p>
                      </div>
                    </div>
                  ))}

                {/* <div className="">
                  <div className="bg-line-active rounded-[10px] w-full max-w-2 h-16 min-h-16">
                  </div>
                </div> */}
              </div>
            </div>
          </div>
        </div>
      </div>
    </>
  );
}
